@import 'variables.scss';
@import "svg-sprite";
@import "png-sprite";


.contacts {
  width: auto;
  margin: 0 auto 40px;
  background-color: #F8F5F5;
  padding: 32px 50px 38px 50px;
  display: flex;
  flex-direction: column;
  //align-items: flex-start;

  &_info {

    .delivery_info_overview_header {
      padding-bottom: 0;
    }

    &_main {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;

      &_connections {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;

        &_delivery {
          padding-top: 70px;
        }
        &_subscribe_content_socials {
          &_vk {
            @include sp__vk_circle-white;

          }
          &_fb {
            @include sp__facebook_circle-white;

          }
          &_twit {
            @include sp__twitter-circle-white;

          }
          &_inst {
            @include sp__instagram_circle-white;

          }
          &_pint {
            @include sp__pinterest-circle-white;

          }
        }
      }

      &_form {
        padding: 23px 30px 30px 30px;
        background-color: #FFFFFF;
        display: flex;
        flex-direction: column;
        width: 442px;
        height: 388px;
        margin-right: 67px;

        &_header {
          color: #231F20;
          font-family: $font_ubuntu;
          font-size: 18px;
          font-weight: 500;
          line-height: 21px;
          padding-bottom: 17px;
        }

        &_rectangle {
          height: 1px;
          width: 442px;
          background-color: #CCC8C4;
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        }

        .about_container_content_vacancy-form_form {
          width: 442px;
          align-items: flex-start;
          height: 330px;
          justify-content: space-between;
          padding-top: 19px;
          &_group {
            width: 443px;
            //padding-bottom: 10px;

            &:last-child {
              padding-bottom: 0;
            }
            &_label {

            }
            &_select {
              width: 323px;

            }

            &_input {
              width: 300px;

            }

            &_submit {
              background-color: #4D4239;
              color: #FFFFFF;
              font-family: $font_ubuntu;
              font-size: 16px;
              line-height: 18px;
              text-align: right;
              font-weight: normal;
            }

            &_textarea {
              width: 300px;
              height: 84px;
            }
          }
        }

      }
    }
  }

  .main_map {
    //width: 1160px;
    //padding: 0;
  }

  &_all {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &_block {
      display: flex;
      flex-direction: column;

      .footer_main_contacts_header {
        padding-bottom: 20px;
      }
    }
  }

  .subscribe_content_socials {
    padding-top: 101px;
    width: 285px;
  }

}